@font-face {
    font-family: 'TisaSansPro';
    src: local("./TisaSansPro-Italic.ttf"),
 url("./TisaSansPro-Italic.ttf") format("truetype");
  }
  @font-face {
    font-family: 'TisaSansPro-Bold';
    src: local("./TisaSansPro-Bold.ttf"),
 url("./TisaSansPro-Bold.ttf") format("truetype");
  }  
.content-container1{
    display: flex;
    width: 70%;
    background: rgba(0, 0, 0, 0.233);
    box-shadow: 0 8px 20px 2px rgba(0, 255, 255, 0.233);
    backdrop-filter: blur( 9px );
    -webkit-backdrop-filter: blur( 9px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    transition: all 0.5s linear;
}
@media screen and (max-width: 700px) {
    .content-container1 {
    flex-direction: column;
    width: 90%;
    }
    }
.content-container1-box1{
    flex: 50%;
}
.content-container1-box2{
    flex: 50%;
}
.container1-box1-txt1{
    color: white;
    font-family: TisaSansPro;
    transition: all 0.5s linear;
}
.container1-box1-txt2{
    color: var(--color-txt1);
    font-size: 3rem;
    text-decoration-line: underline;
    font-family: TisaSansPro-Bold;
    transition: all 0.5s linear;
}
.container1-box1-txt3{
    color: white;
    font-size: 3rem;
    font-family: TisaSansPro-Bold;
    transition: all 0.5s linear;
}
.container1-box2-img1{
    float: left;
    transition: all 0.5s linear;
}
.content-container1-box2-shop{
    color: white;
    font-size: 1.3rem;
    font-family: TisaSansPro-Bold;
    margin-top: 4rem;
    text-shadow: 0px 2px 2px #0ff;
    transition: all 0.5s linear;
}
.content-container2{
    width: 40%;
    background: rgba(0, 0, 0, 0.233);
    box-shadow: 0 8px 20px 2px rgba(0, 255, 255, 0.233);
    backdrop-filter: blur( 9px );
    -webkit-backdrop-filter: blur( 9px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    transition: all 0.5s linear;
}
.content-youtube{
    margin-top: 4rem;
    color: white;
    font-size: 3rem;
    font-family: TisaSansPro-Bold;
    transition: all 0.5s linear;
    text-shadow: 0px 2px 2px var(--color-txt1);
}
@media screen and (max-width: 700px){
    .content-youtube{
        font-size: 2rem;
    }
    .content-container2{
        width: 70%;
    }
    .content-container3{
        width: 70%;
    }
}
@media screen and (max-width: 500px){
    .content-youtube{
        font-size: 1.2rem;
    }
    .content-container2{
        width: 90%;
    }
    .content-container3{
        width: 90% !important;
    }
}
.content-container2-wwe{
    width: 100%;
}
.content-container3{
    width: 40%;
    background: rgba(0, 0, 0, 0.233);
    box-shadow: 0 8px 20px 2px rgba(0, 255, 255, 0.233);
    backdrop-filter: blur( 9px );
    -webkit-backdrop-filter: blur( 9px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    transition: all 0.5s linear;
}
.content-container3-shop{
    width: 100%;
    height: 15rem;
}
