:root{
    --color-txt1: #66fcf1;
}
.home-navbar{
    background-color: rgba(255, 0, 0, 0);
    width: 100%;
    position: fixed;
    background: rgba(0, 0, 0, 0.233);
box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.164);
backdrop-filter: blur( 9px );
-webkit-backdrop-filter: blur( 9px );
border-radius: 10px;
border: 1px solid rgba( 255, 255, 255, 0.18 );
z-index: 1;
}
.home-logo1{
    transition: all 0.5s linear;
    width: 6rem;
    margin-left: 1rem;
    -webkit-filter: drop-shadow(0px 0px 8px rgb(0, 229, 255));
    filter: drop-shadow(0 0 2px #fff, 0 0 10px #fff, 0 0 20px #0ba9ca, 0 0 30px #0ba9ca,
      0 0 40px #0ba9ca, 0 0 50px #0ba9ca);
    
      -webkit-animation: blink 0.7s infinite alternate;
    animation: blink 0.7s infinite alternate;
}
@-webkit-keyframes blink {
    100% {
      box-shadow: 0 0 3px #fff, 0 0 10px #fff, 0 0 20px #fff, 0 0 40px #0ba9ca,
        0 0 70px #0ba9ca, 0 0 80px #0ba9ca;
    }
  }
  
  @keyframes blink {
    100% {
      filter: drop-shadow( 0 0 5px #00d5ff)
    }
  }
.home-navbar-links{
    text-decoration: none;
    font-weight: 600;
    color: var(--color-txt1);
    transition: all 0.5s linear;
}
.home-navbar-links:hover{
    color: #c5c6c7;
    text-shadow: 0px 2px 2px #0ff;
    font-size: 1.09rem;
}
.home-shop-icon{
    color: var(--color-txt1);
    font-size: 1.2rem;
    animation-name: shop;
    animation-duration: 1s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
}
@keyframes shop{
    25%{color: lime;}
    75%{color: #f0f;}
}
.home-navbar-socal{
    float: right;
    display: flex;
    margin-top: -3.5rem;
    margin-right: 4rem;
}
.home-shop-socal-icons{
    margin-left: 1rem;
    text-decoration: none;
    font-weight: 600;
    color: var(--color-txt1);
    transition: all 0.5s linear;
}
/*==================================================navbar for mobile========================================================*/
.home-navbar-mobile{
    background-color: rgba(255, 0, 0, 0);
    width: 100%;
    position: fixed;
    background: rgba(0, 0, 0, 0.233);
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.164);
    backdrop-filter: blur( 9px );
    -webkit-backdrop-filter: blur( 9px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    z-index: 1;
}
.home-logo1-mobile{
    width: 6rem;
    margin-top: -1rem;
    -webkit-filter: drop-shadow(0px 0px 8px rgb(0, 229, 255));
    filter: drop-shadow(0 0 2px #fff, 0 0 10px #fff, 0 0 20px #0ba9ca, 0 0 30px #0ba9ca,
      0 0 40px #0ba9ca, 0 0 50px #0ba9ca);
    
      -webkit-animation: blink 0.7s infinite alternate;
    animation: blink 0.7s infinite alternate;
}
.home-shop-socal-icons-mobile{
    color: var(--color-txt1);
    position: relative;
    top: -1.5rem;
    font-size: 1.7rem;
    width: 5rem;
}